import { createElement } from './utils';
import './components.css';

const playsvg = `<svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor" stroke-width="2">
  <path stroke-linecap="round" stroke-linejoin="round" d="M14.752 11.168l-3.197-2.132A1 1 0 0010 9.87v4.263a1 1 0 001.555.832l3.197-2.132a1 1 0 000-1.664z" />
  <path stroke-linecap="round" stroke-linejoin="round" d="M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
</svg>`;

export function displayReadyAudioBtn(callback) {
  let holder = createElement({
    id: 'tts-extension-holder',
    innerHTML: `Document Summarized!`,
  });
  let playBtn = createElement({
    tag: 'button',
    id: 'tts-extension-btn',
    innerHTML: playsvg,
    append: holder,
  });
  playBtn.addEventListener('click', playDoc);
  function playDoc(e) {
    callback(e);
    holder.remove();
  }
  document.body.append(holder);

  setTimeout(() => {
    holder.remove();
  }, 20000);
}

export function displayAudioText(text) {
  let holder = createElement({
    id: 'tts-extension-holder',
    textContent: text,
    append: document.body,
  });
  return holder;
}
